import React from 'react';

import TabComp from '../tab-comp/TabComp';
import PanelComp from '../tab-comp/PanelComp';
import PodiumStandingList from '../podium-standing-list/PodiumStandingList';

class RaceStanding extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isLoading: true,
            isContructorLoading: true,
            showRacerCompList: 'f2-podium--item-none',
            raceResults: [],
            contructorResults: [],
            error: null
        };  
    }
    
    componentDidMount() {
      this.fetchDrivers();
      this.fetchContructors();
    }

    showFullStanding = () => {
        this.setState({
            showRacerCompList: ''
        });
    }

    fetchDrivers() {
        fetch('http://localhost:3002/drivers')
        .then(response => response.json())
        .then(data =>
            this.setState({
                raceResults: data,
                isLoading: false,
            }))
        .catch(error => this.setState({ error, isLoading: false }));
    }
    
    fetchContructors() {
        fetch('http://localhost:3002/contructors')
        .then(response => response.json())
        .then(data =>
            this.setState({
                contructorResults: data,
                isContructorLoading: false,
            }))
        .catch(error => this.setState({ error, isContructorLoading: false }));
    }

    render() {
        const {
            isLoading, isContructorLoading, raceResults, contructorResults, showRacerCompList, error
        } = this.state;

        const renderRacerList = raceResults.sort((a, b) => b.points - a.points).map((racer, index) => {
            const { id } = racer;    
            return (          
              <li key={id} className={`f2-podium--item ${index > 9 ? showRacerCompList : ''} `}>
                <PodiumStandingList
                  index={index}
                  firstName={racer.firstName}
                  boldName={racer.lastName}
                  simpleName = {racer.team}
                  teamColor = {racer.teamColor}
                  points = {racer.points}
                />
              </li>
            );
        });
        
        const rednerViewFullStanding = (
          <button 
            type="button" 
            className="btn btn--default" 
            onClick={() => this.showFullStanding()} 
            onKeyPress={() => {}}
          >
    View Full Standing
          </button>
    );

    // Constructors List started

    const renderContructorList = contructorResults.sort((a, b) => b.points - a.points).map((contructor, index) => {
        const {
            id,
            drivers, 
            team,
            teamColor, 
            points 
        } = contructor;    
        
        return (          
          <li key={id} className="f2-podium--item">
            <a href="#raceStanding-list" className="f2-podium--link f2-bg--white">
              <span className="f2-podium--rank f2-bold--xs">{index + 1}</span>
              <span className="team-color-icon" style={{ background: `#${teamColor}` }} />
              <span className="f2-podium--driver f2--xs">
                <strong className="f2-podium--surname">{team}</strong>
              </span>
              <span className="f2-podium-subdetail misc--label">{drivers}</span>
              <span className="f2-podium-right">
                <span className="f2-podium--time f2-label misc--label">
                  {points}
                  {' '}
PTS
                </span>
                <i className="icon icon-chevron-right f2-color--warmRed" />
              </span>
            </a>
          </li>
        );
    });

        return (
          <TabComp>
            <PanelComp uniqueKey="panel_1" title="Drivers">
              <div className="container">
                <div className="col-custom-10">
                  <h2 className="f2-raceStanding--tabTitle">Standing</h2>
                  <div className="f2-podium--top-positions">
                    <a href="#podium-2" className="f2-podium--position pos--2">
                      <div className="f2-driver-snapbox">
                        <div className="team-color-border" style={{ background: '#00D2BE' }} />
                        <picture className="f2-driver-snap">
                          <img 
                            alt="v bottas"
                            src="http://localhost:3000/assets/img/v_bottas.png"
                          />
                        </picture>
                      </div>
                      <div className="f2-driver-info">
                        <span className="f2-podium--driver f2--xs">
                          <span className="f2-podium--name">Valtteri</span>
                          <span className="f2-podium--flag">
                            <picture className="team-flag">
                              <img 
                                src="http://localhost:3000/assets/img/country-flag-Greece.png"
                                alt="countryFlag"
                              />
                            </picture>
                          </span>
                          <strong className="f2-podium--surname f2-uppercase">Bottas</strong>
                        </span>
                      </div>
                    </a>

                    <a href="#podium-2" className="f2-podium--position pos--1">
                      <div className="f2-driver-snapbox">
                        <div className="team-color-border" style={{ background: '#00D2BE' }} />
                        <picture className="f2-driver-snap">
                          <img 
                            alt="v bottas"
                            src="http://localhost:3000/assets/img/l_hamilton.png"
                          />
                        </picture>
                      </div>
                      <div className="f2-driver-info">
                        <span className="f2-podium--driver f2--xs">
                          <span className="f2-podium--name">Lewis</span>
                          <span className="f2-podium--flag">
                            <picture className="team-flag">
                              <img 
                                src="http://localhost:3000/assets/img/country-flag-Greece.png"
                                alt="countryFlag"
                              />
                            </picture>
                          </span>
                          <strong className="f2-podium--surname f2-uppercase">hamilton</strong>
                        </span>
                      </div>
                    </a>

                    <a href="#podium-2" className="f2-podium--position pos--3">
                      <div className="f2-driver-snapbox">
                        <div className="team-color-border" style={{ background: '#1E41FF' }} />
                        <picture className="f2-driver-snap">
                          <img 
                            alt="v bottas"
                            src="http://localhost:3000/assets/img/m_verstappen.png"
                          />
                        </picture>
                      </div>
                      <div className="f2-driver-info">
                        <span className="f2-podium--driver f2--xs">
                          <span className="f2-podium--name">Max</span>
                          <span className="f2-podium--flag">
                            <picture className="team-flag">
                              <img 
                                src="http://localhost:3000/assets/img/country-flag-Greece.png"
                                alt="countryFlag"
                              />
                            </picture>
                          </span>
                          <strong className="f2-podium--surname f2-textUppercase">verstappen</strong>
                        </span>
                      </div>
                    </a>
                  </div>
                  {error ? <p>{error.message}</p> : null}
                  <ul className="f2-podium--raceStanding-list">
                    {!isLoading ? (
                        renderRacerList
                        ) : (
                          <h3>Loading...</h3>
                    )}
                    {!isLoading && showRacerCompList.length > 1 ? (rednerViewFullStanding) : ''}
                  </ul>
                </div>
              </div>
                
            </PanelComp>
            <PanelComp uniqueKey="panel_2" title="Contructors">
              <div className="container">
                <div className="col-custom-10">
                  <h2 className="f2-raceStanding--tabTitle">Standing</h2>
                  <div className="f2-podium--top-positions constructors">
                    <a href="#podium-2" className="f2-podium--position pos--2">
                      <div className="f2-contructors-snapbox">
                        {/* <div className="team-color-border" style={{ background: '#00D2BE' }} /> */}
                        <picture className="constructor-logo">
                          <img 
                            alt="v bottas"
                            src="http://localhost:3000/assets/img/ferrari_logo.png"
                          />
                        </picture>
                      </div>
                      <div className="f2-constructorsCar-info">
                        <picture className="car-image">
                          <img 
                            src="http://localhost:3000/assets/img/ferrari.png"
                            alt="ferrari"
                          />
                        </picture>
                        {/* <div className="team-color-border" style={{ background: '#00D2BE' }} /> */}
                      </div>
                    </a>

                    <a href="#podium-2" className="f2-podium--position pos--1">
                      <div className="f2-contructors-snapbox">
                        <picture className="constructor-logo">
                          <img 
                            alt="v bottas"
                            src="http://localhost:3000/assets/img/mercedes_logo.png"
                          />
                        </picture>
                        {/* <div className="team-color-border" style={{ background: '#00D2BE' }} /> */}
                      </div>
                      <div className="f2-constructorsCar-info">
                        <picture className="car-image">
                          <img 
                            src="http://localhost:3000/assets/img/mercedes.png"
                            alt="ferrari"
                          />
                        </picture>
                      </div>
                    </a>

                    <a href="#podium-2" className="f2-podium--position pos--3">
                      <div className="f2-contructors-snapbox">
                        {/* <div className="team-color-border" style={{ background: '#00D2BE' }} /> */}
                        <picture className="constructor-logo">
                          <img 
                            alt="v bottas"
                            src="http://localhost:3000/assets/img/redBull_logo.png"
                          />
                        </picture>
                      </div>
                      <div className="f2-constructorsCar-info">
                        <picture className="car-image">
                          <img 
                            src="http://localhost:3000/assets/img/redBull.png"
                            alt="ferrari"
                          />
                        </picture>
                      </div>
                    </a>
                  </div>
                
                  {error ? <p>{error.message}</p> : null}
                  <ul className="f2-podium--raceStanding-list">
                    {!isContructorLoading ? (
                        renderContructorList
                        ) : (
                          <h3>Loading...</h3>
                    )}
                  </ul>
                </div>
              </div>
            </PanelComp>
            <PanelComp uniqueKey="panel_3" title="Last Race">This is the lastrace panel</PanelComp>
          </TabComp>
        );
    }
}

export default RaceStanding;
